import React, { Component } from 'react'
import NavBar from '../../Components/NavBar/NavBar';
import './index.css';

export default class EntranceService extends Component {
  render() {
    return (
      <div className="entranceService-wrapper">
        <NavBar backgroundColor="#fff" boxShadow="0 0 10px #ccc" fixed="true"/>
        <div className="col-md-10 col-md-offset-1">
          <div className="entranceService-top row">
            <div className="service-top-body-left col-md-6 col-sm-6">
              <h1></h1>
              <p></p>
              <a href="#"></a>
            </div>
            <div className="service-top-body-right col-md-5 col-sm-6 col-md-offset-1">
              <img className="img-responsive" src={`${require('../../images/entranceService.png')}`} alt=""/>
            </div>
          </div>
          <div className="EntranceService-bottom row">
            <div className="col-md-3 col-sm-6 plan" style={{borderTopLeftRadius: "0.1rem"}}>
              <div className="hidden-plan">
                <h2></h2>
                <p>AI</p>
              </div>
              <h2></h2>
              <p>Plan of <br/>
                Oversea Study
              </p>
            </div>
            <div className="col-md-3 col-sm-6 studyexperience">
              <div className="hidden-studyexperience">
                <h2></h2>
                <p></p>
              </div>
              <h2></h2>
              <p>Experience of <br/>
              Study Tour
              </p>
            </div>
            <div className="col-md-3 col-sm-6 training">
              <div className="hidden-training">
                <h2></h2>
                <p></p>
              </div>
              <h2></h2>
              <p>
                Pre Examination <br/>
                training
              </p>
            </div>
            <div className="col-md-3 col-sm-6 application"  style={{borderTopRightRadius: "0.1rem"}}>
              <div className="hidden-application">
                <h2></h2>
                <p></p>
              </div>
              <h2></h2>
              <p>Application <br/>
                Submission
              </p>
            </div>
            <div className="col-md-3 col-sm-6 document">
              <div className="hidden-document">
                <h2></h2>
                <p></p>
              </div>
              <h2></h2>
              <p>
                Document <br/>
                Writing
              </p>
            </div>
            <div className="col-md-3 col-sm-6 tracking">
              <div className="hidden-tracking">
                <h2></h2>
                <p></p>
              </div>
              <h2></h2>
              <p>Enrollment Progress <br/>
                Tracking
              </p>
            </div>
            <div className="col-md-3 col-sm-6 visa">
              <div className="hidden-visa">
                <h2></h2>
                <p></p>
              </div>
              <h2></h2>
              <p>Visa <br/>
                Processing
              </p>
            </div>
            <div className="col-md-3 col-sm-6 overseas">
              <div className="hidden-overseas">
                <h2></h2>
                <p></p>
              </div>
              <h2></h2>
              <p>overseas Life <br/>
                Assistance
              </p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
