import request from '../untils/request'

//  
export function doLoginGet() {
  return request({
    url: `/api/auth/login`,
    method: 'get'
  })
}

// 
export function doLoginPost(data) {
  return request({
    url: `/api/auth/login`,
    method: 'post',
    data,
  })
}

// 
export function logout() {
  return request({
    url: `api/auth/logout`,
    method: "get",
  })
}
