import request from '../untils/request'

// -
export function creditLog(params) {
  return request({
    url: '/api/user/credit/list',
    method: 'get',
    params,
  })
}


// 
export function baseInfoGet(params) {
  return request({
    url: '/api/user/baseInfo',
    method: "get",
    params,   
  })
}

// 

export function baseInfoPost(data) {
  return request({
    url: '/api/user/baseInfo',
    method: "post",
    data,
  })
}

